.visually-hidden:not(:focus):not(:active) {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@mixin title {
  font-weight: 800;
  font-size: 37px;
  color: $BlackBlue;
}

@mixin list {
  margin: 0;
  padding: 0;
  list-style: none;
}
